t-family,\n      'text-transform': false\n    ),\n    'button': (\n      'size': .875rem,\n      'weight': 500,\n      'line-height': 2.25rem,\n      'letter-spacing': .0892857143em,\n      'font-family': $body-font-family,\n      'text-transform': uppercase\n    ),\n    'caption': (\n      'size': .75rem,\n      'weight': 400,\n      'line-height': 1.25rem,\n      'letter-spacing': .0333333333em,\n      'font-family': $body-font-family,\n      'text-transform': false\n    ),\n    'overline': (\n      'size': .75rem,\n      'weight': 500,\n      'line-height': 2rem,\n      'letter-spacing': .1666666667em,\n      'font-family': $body-font-family,\n      'text-transform': uppercase\n    )\n  ),\n  $headings\n);\n\n$typography: () !default;\n@each $type, $values in $headings {\n  $typography: map-deep-merge(\n    $typography,\n    (#{$type}: map-values($values))\n  );\n}\n\n$transition: () !default;\n$transition: map-deep-merge(\n  (\n    'fast-out-slow-in': cubic-bezier(0.4, 0, 0.2, 1),\n    'linear-out-slow-in': cubic-bezier(0, 0, 0.2, 1),\n    'fast-out-linear-in': cubic-bezier(0.4, 0, 1, 1),\n    'ease-in-out': cubic-bezier(0.4, 0, 0.6, 1),\n    'fast-in-fast-out': cubic-bezier(0.25, 0.8, 0.25, 1),\n    'swing': cubic-bezier(0.25, 0.8, 0.5, 1)\n  ),\n  $transition\n);\n$primary-transition: 0.3s map-get($transition, 'swing') !default;\n$secondary-transition: 0.2s map-get($transition, 'ease-in-out') !default;\n\n// Ripples //;\n$ripple-animation-transition-in: transform 0.25s map-get($transition, 'fast-out-slow-in'), opacity 0.1s map-get($transition, 'fast-out-slow-in') !default;\n$ripple-animation-transition-out: opacity 0.3s map-get($transition, 'fast-out-slow-in') !default;\n$ripple-animation-visible-opacity: 0.15 !default;\n\n// Elements //;\n$bootable-transition: 0.2s map-get($transition, 'fast-out-slow-in') !default;\n$blockquote-font-size: 18px !default;\n$blockquote-font-weight: 300 !default;\n$code-kbd-border-radius: 3px !default;\n$code-kbd-font-size: 85% !default;\n$code-kbd-font-weight: normal !default;\n$code-padding: .2em .4em !default;\n$kbd-padding: .2em .4rem !default;\n$input-top-spacing: 16px !default;\n$text-field-active-label-height: 12px !default;\n","@import '../../styles/styles.sass';\n@import '../../styles/tools/_functions.sass';\n\n$btn-active-opacity: 0.18 !default;\n$btn-border-radius: $border-radius-root !default;\n$btn-focus-opacity: 0.24 !default;\n$btn-font-weight: 500 !default;\n$btn-hover-opacity: 0.08 !default;\n$btn-icon-font-size: 18px !default;\n$btn-icon-padding: 12px !default;\n$btn-letter-spacing: .0892857143em !default;\n$btn-outline-border-width: thin !default;\n$btn-round-border-radius: 50% !default;\n$btn-rounded-border-radius: 28px !default;\n$btn-text-transform: uppercase !default;\n$btn-tile-border-radius: 0 !default;\n$btn-transition-duration: 0.28s !default;\n$btn-transition-fn: map-get($transition, 'fast-out-slow-in') !default;\n$btn-transition: opacity 0.2s map-get($transition, 'ease-in-out') !default;\n$btn-states: (\n  'states': (\n    'activated': $btn-active-opacity,\n    'focus': $btn-focus-opacity,\n    'hover': $btn-hover-opacity\n  )\n);\n$btn-sizes: () !default;\n$btn-sizes: map-deep-merge(\n  (\n    'x-small': 20,\n    'small': 28,\n    'default': 36,\n    'large': 44,\n    'x-large': 52\n  ),\n  $btn-sizes\n);\n\n$btn-font-sizes: () !default;\n$btn-font-sizes: map-deep-merge(\n  (\n    'x-small': .625rem,\n    'small': .75rem,\n    'default': .875rem,\n    'large': .875rem,\n    'x-large': 1rem\n  ),\n  $btn-font-sizes\n);\n\n$fab-sizes: () !default;\n$fab-sizes: map-deep-merge(\n  (\n    'x-small': 32,\n    'small': 40,\n    'default': 56,\n    'large': 64,\n    'x-large': 72\n  ),\n  $fab-sizes\n);\n\n$fab-icon-sizes: () !default;\n$fab-icon-sizes: map-deep-merge(\n  (\n    'x-small': 18,\n    'small': 24,\n    'default': 24,\n    'large': 28,\n    'x-large': 32\n  ),\n  $fab-icon-sizes\n);\n","@mixin rtl()\n  .v-application--is-rtl &\n    @content\n\n@mixin ltr()