enerate-utility-body($utility, $properties, $value, $infix)\n\n=generate-utility-body($utility, $properties, $value, $infix)\n  // Use custom class if present\n  $property-class: map-get($utility, class)\n  $property-class: if($property-class, $property-class, nth($properties, 1))\n\n  // Don't prefix if value key is null (eg. with shadow class)\n  $property-class-modifier: if(nth($value, 1), \"-\" + nth($value, 1), \"\")\n\n  $value: nth($value, 2)\n\n  .#{$property-class + $infix + $property-class-modifier}\n    @for $i from 1 through length($properties)\n      $property: nth($properties, $i)\n      $val: $value\n      @if type-of($value) == 'list' and length($properties) == length($value)\n        $val: nth($value, $i)\n      @if $val != false\n        // Check if unimportant property exists.\n        // This allows you to conditional skip\n        // defining a property as important.\n        $unimportant: map-get($utility, unimportant)\n        #{$property}: $val if(index($unimportant, $property), null, !important)\n","@import './_functions'\n@import './_bootable'\n@import './_elevation'\n@import './_radius'\n@import './_rtl'\n@import './_sheet'\n@import './_states'\n@import './_theme'\n@import './_utilities'\n@import './_display'\n\n=media-breakpoint-up($name, $breakpoints: $grid-breakpoints)\n  $min: breakpoint-min($name, $breakpoints)\n  @if $min\n    @media (min-width: $min)\n      @content\n  @else\n    @content\n","@use \"sass:string\"\n\n@import './_display.sass'\n@import './_screenreaders.sass'\n@import './_text.sass'\n@import './_transition.sass'\n\n// Utilities\n@each $breakpoint in map-keys($grid-breakpoints)\n  // Generate media query if needed\n  +media-breakpoint-up($breakpoint)\n    $infix: breakpoint-infix($breakpoint, $grid-breakpoints)\n\n    // Loop over each utility property\n    @each $key, $utility in $utilities\n      // The utility can be disabled with `false`, thus check if the utility is a map first\n      // Only proceed if responsive